import React, { useState } from 'react'
import { Card, Table, Button, Tooltip } from 'antd';
import Flex from 'components/shared-components/Flex'
import { Typography } from "antd";
import NumberFormat from 'react-number-format';
import { useHistory } from "react-router-dom";
import { PlusCircleOutlined, EyeOutlined, DeleteOutlined  } from '@ant-design/icons';
import TrucksList from '../invoice';

const { Title } = Typography;

const Garage = ({systemData}) => {
	let history = useHistory();
	const newRent = row => {
		history.push(`/app/apps/truckRental/add`)
	}

  	const { allTrucks, rentedTrucks, rentedTrucksLoading, allCurrencyLoading, allCurrency, rental, rentalLoading, allRentedTrucks, trips, tripsLoading, allDestinations, allDestinationsLoading, countries, countriesLoading, currencies, currenciesLoading, orders, ordersLoading} = systemData;
	const [trucksRented, setTrucksRented] = useState(null)
	const [listVisible, setListVisible] = useState(false)
	const [rentalInfo, setRentalInfo] = useState({});

	const showRentedList = info => {
		setRentalInfo(info)
		let allTrucks = rentedTrucksLoading ? [] : rentedTrucks.docs.filter(x=> x.data().rentId == info._id);
		setTrucksRented(allTrucks);
		setListVisible(true)
	};
	
	const closeGarageList = () => {
		setListVisible(false)
		setRentalInfo({})
		setTrucksRented({});
	}

	const tableColumns = [
		{
			title: 'Date',
			dataIndex: 'id',
			render: (_,record) => new Date(+record.data()._id).toLocaleString()
		},
		{
			title: 'Company Name',
			dataIndex: 'companyName',
			render: (_,record) => record.data().companyName
		},
		{
			title: 'Company Number',
			dataIndex: 'companyNumber',
			render: (_, record) => record.data().companyNumber
		},
		{
			title: 'Total trucks',
			dataIndex: 'trucks',
			render: (_, record) => record.data().trucks
		},
		// {
		// 	title: 'Price',
		// 	dataIndex: 'amount',
		// 	render: (_, record) => (
		// 			  <div>
		// 				  <NumberFormat
		// 					  displayType={'text'} 
		// 					  value={record.data().amount} 
		// 					  suffix={` ${allCurrencyLoading ? '' : allCurrency[record.data().currency]?.name}`} 
		// 					  thousandSeparator={true} 
		// 				  />
		// 			  </div>
		// 		  ),
		// },
		
		// {
		// 	title: 'Rate',
		// 	dataIndex: 'exchangeRate',
		// 	render: (_, record) => (
		// 			  <div>
		// 				  <NumberFormat
		// 					  displayType={'text'} 
		// 					  value={record.data().exchangeRate} 
		// 					  thousandSeparator={true} 
		// 				  />
		// 			  </div>
		// 		  ),
		// },
		// {
		// 	title: 'Total RWF',
		// 	dataIndex: 'totalAmount',
		// 	render: (_, record) => (
		// 			  <div>
		// 				  <NumberFormat
		// 					  displayType={'text'} 
		// 					  value={record.data().totalAmount} 
		// 					  suffix={' RWF'} 
		// 					  thousandSeparator={true} 
		// 				  />
		// 			  </div>
		// 		  ),
		// },
		{
			title: '',
			dataIndex: 'actions',
			render: (_, elm) => (
				<div className="text-right">
					<Tooltip title="View">
						<Button type="primary" className="mr-2" icon={<EyeOutlined />} onClick={() => {showRentedList(elm.data())}} size="small"/>
					</Tooltip>
				</div>
			)
		}

	];

	return (
		<>
			{!listVisible ? <Card>
				<Flex alignItems="center" justifyContent="between" mobileFlex={false}>
						<Flex className="mb-1" mobileFlex={false}>
							<div className="mr-md-3 mb-3">
								<Title level={4}>Truck rental</Title>
							</div>
						</Flex>
						<div>
							<Button size="small" onClick={newRent} type="primary" icon={<PlusCircleOutlined />} block>New Rental</Button>
						</div>
					</Flex>
				<div className="table-responsive">
					<Table 
						columns={tableColumns} 
						dataSource={!rentalLoading ? rental.docs.filter(x=> x.data().status).reverse() : []} 
						loading={rentalLoading}
						rowKey='_id' 
					/>
				</div>
			</Card>
			:
			<TrucksList 
				rentedTrucksLoading={rentedTrucksLoading}
				rentedTrucks={rentedTrucks}
				trucksRented={trucksRented} 
				rentalInfo={rentalInfo} 
				allTrucks={allTrucks}
				allCurrency={allCurrency}
				allCurrencyLoading={allCurrencyLoading}
				allRentedTrucks={allRentedTrucks}
				trips={trips}
				tripsLoading={tripsLoading}
				allDestinationsLoading={allDestinationsLoading}
				allDestinations={allDestinations}
				countries={countries} 
				countriesLoading={countriesLoading} 
				currencies={currencies}
				currenciesLoading={currenciesLoading}
				orders={orders}
				ordersLoading={ordersLoading}
				closeGarageList={closeGarageList} 
			/>}
		</>
	)
}

export default Garage
