import React, {useState} from 'react'
import { PrinterOutlined, DeleteOutlined, BgColorsOutlined, CloseCircleOutlined, ArrowLeftOutlined } from '@ant-design/icons';
import { Card, Table, Input, InputNumber, Button, Form, Modal, message, Row, Col, Menu, Select } from 'antd';
import NumberFormat from 'react-number-format';
import { useHistory } from "react-router-dom";
import { DataService } from "services/rental.service";
import { DataService as RentedService } from "services/rentedTrucks.service";
import { DataService as FuelService } from "services/fuel.service";

import EllipsisDropdown from 'components/shared-components/EllipsisDropdown';
import Flex from 'components/shared-components/Flex'
import { Typography } from "antd";

const { Column } = Table; 
const { Title } = Typography;
const { confirm } = Modal;
const { Option } = Select;

const Invoice = (props) => { 	
	const [form] = Form.useForm();

	let history = useHistory();	
	const [showFuel, setShowFuel] = useState(false);
	const [currentTruck, setCurrentTruck] = useState('');
	const [editingKey, setEditingKey] = useState('');
	const [allTrips, setAllTrips] = useState([]);
	const [liter, setLiter] = useState(0);
	const [rate, setRate] = useState(0);
	const [price, setPrice] = useState(0);
	const [totalAmount, setTotalAmount] = useState(0);
	const [confirmLoading, setConfirmLoading] = useState(false);
	const [assignLoading, setAssignLoading] = useState(false);

	const { trucksRented, rentalInfo, allCurrency, allCurrencyLoading, rentedTrucksLoading, rentedTrucks, allRentedTrucks, trips, tripsLoading, allDestinations, allDestinationsLoading, countries, countriesLoading, currencies, currenciesLoading,  orders, ordersLoading} = props;
	let todayDate = new Date().getTime();
	
	let tripOrders = [];
	if(!tripsLoading){
	trips.docs.map((doc)=>{
			tripOrders = {
				...tripOrders,
				[doc.data()._id] : doc.data().orderId,
			}
		})
	}

	let allRecords = [];
  	if(!ordersLoading){
		orders.docs.map((doc)=>{
			allRecords = {
				...allRecords,
				[doc.data()._id] : {'client':doc.data().client, 'item':doc.data().item, 'departure':doc.data().departure, 'destination':doc.data().destination},
			}
		})
	}

	const addFuel = (row) => {
		setCurrentTruck(allRentedTrucks[+row._id] ? `${allRentedTrucks[row._id].plateNumber} ${allRentedTrucks[row._id].driverName}` : '');
		let trps = tripsLoading ? [] : trips.docs.filter(x => x.data().truckId == row._id && x.data().status == 'En Route');
    	setAllTrips(trps);
		setEditingKey(row._id);
		setShowFuel(true);
	};

	const handleClose = () => {
		setShowFuel(false);
		setTotalAmount(0);
		setEditingKey();
	}

	const handleRateChange = (value) => {
		let total = value*liter*price;
		setRate(value);
		setTotalAmount(total);
	  }
	
	  const handleLiterChange = (value) => {
		let total = rate*price*value;
		setLiter(value);
		setTotalAmount(total);
	  }
	
	  const handlePriceChange = (value) => {
		let total = rate*liter*value;
		setPrice(value);
		setTotalAmount(total);
	  }

	const RedirectBack = () => {
		history.push(`/app/apps/truckRental/list`)
	}	

	function showConfirm(row) {
		confirm({
		  title: `Do you want to delete this rental information of ${row.companyName}?`,
		  onOk() {
			deleteRow(row)
		  },
		  onCancel() {},
		});
	}

	const deleteRow = row => {
		let allTrucks = rentedTrucksLoading ? [] : rentedTrucks.docs.filter(x=> x.data().rentId == row._id);
		allTrucks.map(truck=>{
			let obj = truck.data();
			obj.status = false;
			RentedService.update(truck.id,obj)
		})
		row.status = false;
		DataService.update(row._id,row);
		message.success({ content: `Rental info have been deleted `, duration: 3 });
	}

	const dropdownMenu = row => (
		<Menu>
      		{row.expiration > todayDate ? <Menu.Item onClick={() => {addFuel(row)}}>
				<Flex alignItems="center">
					<BgColorsOutlined />
					<span className="ml-2">Add Fuel</span>
				</Flex>
			</Menu.Item>
			:
			<Menu.Item>
				<Flex alignItems="center">
					<span className="ml-2">Expired</span>
				</Flex>
			</Menu.Item>}
		</Menu>
	);

	const handleAddFuel = async e => {
		try {
		  const values = await form.validateFields();
		  let meterNumber = values.meterNumber;
		  let liters = values.liters;
		  let literPrice = values.literPrice;
		  let station = ''+values.station.slice(0,1) === ' ' ? ''+values.station.slice(1) : values.station;
		  let country = values.country;
		  let currency = values.currency;
		  let exchangeRate = values.exchangeRate;
		  let tripId = +values.tripId;
		  let orderId = +tripId ? tripOrders[tripId] : '';
		  if (liters === '' || literPrice === '' || station === '' || meterNumber === '' || country === '' || currency === '' || !editingKey || !totalAmount) return;
		  
	
		  setConfirmLoading(true);
		  let fuelData = { meterNumber:0,liters,literPrice,station,country,exchangeRate,truckId:editingKey, currency, totalAmount, tripId, orderId, internal:false }
		  DataService.update(editingKey,{meterNumber});
		  FuelService.create(fuelData);
		  handleClose();
		  history.push(`/app/apps/fuels`)
		  message.success(`Fuel info added`);
		} catch (errInfo) {}
		
	  };

	return (
		<div className="container">
			<Card>
				<div className="d-md-flex justify-content-md-between">
					<div>
						<img src="/img/logo.png" alt=""/>
						<address>
							<p>
								<span className="font-weight-semibold text-dark font-size-md">Great lakes</span><br />
								<span>Company Name: {rentalInfo.companyName}</span><br />
								<span>Company Number: {rentalInfo.companyNumber}</span><br />
								<span>Total trucks : {rentalInfo.trucks}</span><br />
								{/* <span>Amount : {rentalInfo.amount} {allCurrencyLoading ? '' : allCurrency[rentalInfo.currency]?.name}</span><br />
								<span>Exchange Rate : <NumberFormat displayType={'text'} value={rentalInfo.exchangeRate} thousandSeparator={true} /></span><br />
								<span>Amount in RWF : <NumberFormat displayType={'text'} value={rentalInfo.totalAmount} suffix=" Rwf" thousandSeparator={true} /></span><br /> */}
								<span>Date rented :{new Date(+rentalInfo._id).toLocaleString()}</span><br />
							</p>
						</address>
					</div>
					<div className="mt-3 text-right">
						<h2 className="mb-1 font-weight-semibold">Truck rental</h2>
						<p>{new Date(rentalInfo.date).toLocaleDateString()}</p>
						<address>
							<p>
								<span className="font-weight-semibold text-dark font-size-md">{rentalInfo.companyName}</span><br />
								<span>{rentalInfo.companyNumber}</span><br />
							</p>
						</address>
					</div>
				</div>
				

				<div className="mt-3">
					<h2 className="mb-1 font-weight-semibold">Trucks Rented</h2>
					<Table dataSource={trucksRented} pagination={false} className="mb-5">
						
						<Column title="Driver Name" 
							render={(_,record) => record.data().driverName} 
							key="driverName"
						/>

						<Column title="ID/Passport Number" 
							render={(_,record) => record.data().idNumber} 
							key="idNumber"
						/>
{/* 
						<Column title="Passport" 
							render={(_,record) => record.data().passportNumber} 
							key="passportNumber"
						/> */}

						<Column title="Contact" 
							render={(_,record) => record.data().phoneNumber} 
							key="contact"
						/>
						
						<Column title="Plate Number" 
							render={(_,record) => record.data().plateNumber} 
							key="plateNumber"
						/>

						<Column title="Trailer Number" 
							render={(_,record) => record.data().trailerNumber} 
							key="trailerNumber"
						/>
						
						{/* <Column title="Trips" 
							render={(_,record) => (
								<NumberFormat 
									displayType={'text'} 
									value={record.data().trips} 
									thousandSeparator={true}
								/>
							)} 
							key="trips"
						/> */}

						
{/* 						
						<Column title=" " 
							render={(_,record) => (<div className="text-right"><EllipsisDropdown menu={dropdownMenu(record.data())}/></div>)} 
							key="action"
						/>
						 */}
					</Table>
				</div>
				
				{/* <div className="mt-3">
					
					<div className="d-flex justify-content-end mt-4">
						<div className="text-right ">
							
							<h2 className="font-weight-semibold mt-3">
								<span className="mr-1">Grand Total: </span>
								<NumberFormat 
									displayType={'text'} 
									value={rentalInfo.totalAmount} 
									suffix={'Rwf'} 
									thousandSeparator={true}
								/>
							</h2>
						</div>
					</div>
				</div> */}

				<hr className="d-print-none"/>
				<div className="text-right d-print-none">
					<Button type="secondary" className="mr-2" onClick={() => RedirectBack()}>
						<ArrowLeftOutlined /> Back
					</Button>
					<Button type="primary" className="mr-2" onClick={() => window.print()}>
						<PrinterOutlined  type="printer" />
						<span className="ml-1">Print</span>
					</Button>
					<Button danger className="mr-2" onClick={() => {showConfirm(rentalInfo)}}>
						<DeleteOutlined />
						<span className="ml-1">Delete</span>
					</Button>
				</div>

			</Card>
			<Form form={form} component={false} layout="vertical">
				<Modal
				title={`Add fuel in truck ${currentTruck}`}
				visible={showFuel}
				onOk={handleAddFuel}
				onCancel={handleClose}
				>
				<Row gutter={16}>
					<Col xs={24} sm={24} md={24}>
					<Form.Item
						label="Specify Fuel for what"
						name="tripId"
						rules={[{ required: true,message: `Specification is required` }]}
						style={{ display: 'block'}}
						className="w-100" 
					>
						<Select
						className="w-100"
						placeholder="-Select-"
						>
							<Option value='0'>Internal Use</Option>
							{allTrips.map((trip)=><Option value={trip.data()._id}>
							{allDestinationsLoading ? '' : allDestinations[allRecords[trip.data().orderId]?.departure]?.name +' - '+allDestinations[allRecords[trip.data().orderId]?.departure]?.country}
							{' to '} 
							{allDestinationsLoading ? '' : allDestinations[allRecords[trip.data().orderId]?.destination]?.name +' - '+allDestinations[allRecords[trip.data().orderId]?.destination]?.country}
							{' with '+trip.data().milleage+' RWF'}
							</Option>)}
						</Select>
									</Form.Item>
					</Col>
					{/* <Col xs={24} sm={12} md={12}>
					<Form.Item name="meterNumber" label="Current meter number" rules={[
						{
						required: true,
						message: `meter number is required.`,
						},
					]}>
						<InputNumber
						className="w-100"
						min={0}
						formatter={value => `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, '')}
						parser={value => value.replace(/\$\s?|(,*)/g, '')}
						/>
					</Form.Item>
					</Col> */}
					
					<Col xs={24} sm={12} md={12}>
					<Form.Item name="liters" label="Liters added" rules={[
						{
						required: true,
						message: `How many liters were added`,
						},
					]}>
						<InputNumber
						className="w-100"
						min={0}
						onChange={handleLiterChange}
						formatter={value => `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, '')}
						parser={value => value.replace(/\$\s?|(,*)/g, '')}
						/>
					</Form.Item>
					</Col>
					<Col xs={24} sm={12} md={12}>
					<Form.Item name="station" label="Station" rules={[
						{
						required: true,
						message: `station is required.`,
						},
					]}>
						<Input
						className="w-100"                      
						/>
					</Form.Item>
					</Col>
					<Col xs={24} sm={12} md={12}>
					<Form.Item name="country" label="Select which country" rules={[
						{
						required: true,
						message: `Country is required.`,
						},
					]}>
						<Select
						className="w-100 d-block"
						placeholder="- Select Country -"
						>
							{!countriesLoading ? countries.docs.map((country)=><Option value={country.data()._id}>{country.data().name}</Option>) : null }
						</Select>
					</Form.Item>
					</Col>
					<Col xs={24} sm={12} md={12}>
					<Form.Item name="literPrice" label="Price per liter" rules={[
						{
						required: true,
						message: `Price per liter is required.`,
						},
					]}>
						<InputNumber
						className="w-100"
						min={0}
						onChange={handlePriceChange}
						formatter={value => `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, '')}
						parser={value => value.replace(/\$\s?|(,*)/g, '')}
						/>
					</Form.Item>
					</Col>
					<Col xs={24} sm={12} md={12}>
					<Form.Item name="currency" label="Select currency used" rules={[
						{
						required: true,
						message: `Currency is required.`,
						},
					]}>
						<Select
						className="w-100 d-block"
						placeholder="- Select Currency -"
						>
							{!currenciesLoading ? currencies.docs.map((currency)=><Option value={currency.data()._id} className='w-100 pe-5'>{currency.data().name}&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</Option>) : null }
						</Select>
					</Form.Item>
					</Col>
					<Col xs={24} sm={12} md={12}>
					<Form.Item name="exchangeRate" label="Exchange Rate vs 1RWF" rules={[
						{
						required: true,
						message: `rate is required.`,
						},
					]}>
						<InputNumber
						className="w-100"
						min={0}
						onChange={handleRateChange}
						formatter={value => `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, '')}
						parser={value => value.replace(/\$\s?|(,*)/g, '')}
						/>
					</Form.Item>
					</Col>
					<Col xs={24} sm={24} md={24}>
					<Title level={4}>
						<NumberFormat
						displayType={'text'} 
						value={totalAmount}
						suffix={'RWF'} 
						prefix={'Total RWF: '} 
						thousandSeparator={true} 
						/>
						<Form.Item >
						<Input
							className="w-100 mt-2" value={totalAmount}                      
						/>
						</Form.Item>
					</Title>
					</Col>
				</Row>
				</Modal>  
			</Form>
		</div>
	);
}

export default Invoice
