import React, { useState } from 'react'
import { SaveOutlined, MinusCircleOutlined, PlusOutlined, CloseCircleOutlined } from '@ant-design/icons';
import { Form, Card, Col, Row, Select, Button, DatePicker, Input, InputNumber, message, Divider } from 'antd';
import NumberFormat from 'react-number-format';
import moment from 'moment';
import { useHistory } from "react-router-dom";
import { DataService } from "services/rental.service";
import { DataService as RentedService } from "services/rentedTrucks.service";
// import { DataService as RecordsService } from "services/garageRecords.service";
// import Invoice from '../invoice';

const { TextArea } = Input;
const dateFormat = 'DD/MM/YYYY';
const { Option } = Select;
let offGrid = {
	visible: false,
	receiptTotal: 0,
	receiptId: '',
	salesInfo: {},
	shopName: {},
	orderDetails: [
		{ driverName: '', phoneNumber: '',  idNumber: '', passportNumber: '', plateNumber: '', trailerNumber: '', trips:'1' }
	  ]
};
const Sales = ({systemData}) => { 	
	const [form] = Form.useForm();
	const [dates, setDates] = useState(new Date());
	const [companyName, setCompanyName] = useState('');
	const [companyNumber, setCompanyNumber] = useState('');
	const [grandTotal, setGrandTotal] = useState(0);
	const [rate, setRate] = useState(1);
	const [totalAmount, setTotalAmount] = useState(0);

	const [visible, setVisible] = useState(offGrid.visible);
	const [selectedCurrency, setSelectedCurrency] = useState();
	const [buttonLoading, setButtonLoading] = useState(false);
	const [inputFields, setInputFields] = useState([
	  { driverName: '', phoneNumber: '',  idNumber: '', passportNumber: '', plateNumber: '', trailerNumber: '', trips:'1' }
	]);
	const { userData,  allCurrencyLoading, allCurrency, currencies, currenciesLoading, addDays } = systemData; 
	let history = useHistory();	
	const RedirectBack = () => {
		history.push(`/app/apps/truckRental/list`)
	}
	const handleSubmit = async () => {
		setButtonLoading(true)
		try {
			const row = await form.validateFields();
			let values = [...inputFields];
			let recId = dates;
			offGrid.receiptId = recId;
			let recordId = +(recId.getTime());
			let records = {
				date: recordId,
				companyName,
				companyNumber,
				userId : userData.userId,
				backupUser:userData.displayName,
			}
			
			let rentId = +(recId.getTime());
			let data = {
				date: dates.getTime(),
				trucks: values.length,
				amount: grandTotal,
				totalAmount,
				exchangeRate: rate,
				companyName,
				companyNumber,
				userId : userData.userId,				
				backupUser:userData.displayName,
				status:true,
			}
			let customer = {
				companyName,
				companyNumber,
			}
			values.map(function(record){
				let truckInfo = {
					date:+(dates.getTime()),
					driverName : record.driverName,
					phoneNumber : record.phoneNumber,
					idNumber : record.idNumber,
					passportNumber : record.passportNumber,
					plateNumber : record.plateNumber,
					trailerNumber : record.trailerNumber,
					trips : record.trips,
					rentId,
					backupUser:userData.displayName,
				};
				return RentedService.create(truckInfo);
			});
			setTimeout(() => {
				offGrid = {
					visible: false,
					receiptTotal: 0,
					receiptId: '',
					salesInfo: {},
					shopName: {},
					orderDetails: [
						{ driverName: '', phoneNumber: '',  idNumber: '', passportNumber: '', plateNumber: '', trailerNumber: '', trips:'1' }
					  ]
				};
			  }, 3000);
			DataService.create(data,rentId);
			offGrid.receiptTotal = grandTotal;
			offGrid.orderDetails = values;
			offGrid.salesInfo = data;
			
			setInputFields([{ driverName: '', phoneNumber: '',  idNumber: '', passportNumber: '', plateNumber: '', trailerNumber: '', trips:'1' }]);
			setGrandTotal(0);
			setRate(0);	
			setTotalAmount(0);	
			setCompanyName('');
			setCompanyNumber('');
			setButtonLoading(false)
			RedirectBack();
			//offGrid.visible = true;
			//setVisible(offGrid.visible);
			message.success("Truck rental have been recorded!")
		} catch (errInfo) {
			console.log(errInfo)
			setButtonLoading(false)
		}	
	};
	
	const handleAddFields = () => {
		const values = [...inputFields];
		values.push({ driverName: '', phoneNumber: '',  idNumber: '', passportNumber: '', plateNumber: '', trailerNumber: '', trips:'1' });
		setInputFields(values);
	};
	
	const handleDateChange = date => {
		setDates(date ? new Date(date.format('YYYY-MM-DD HH:mm:ss')) : new Date());
	};
			
	const handleNameChange = event => {
		setCompanyName(event.target.value);
	};
	
	const handleNumberChange = value => {
		let newValue = ((!value && value !== 0 )|| value === '') ? '' : ((value+'').length > 9 ? (value+'').slice(0,9) : parseInt(value));
		let newTin = ((!value && value !== 0 )|| value === '') ? '' : parseInt(value);
		let check = (value+'').slice(0,2);

		if(newValue){
			newValue = check === ('78' || '79' || '73' || '72') ? ('0' + newValue) : newTin;
		}
		setCompanyNumber(newValue);
		form.setFieldsValue({'companyNumber': newValue});
	};

	const handleDriverName = (index, event) => {	
		const values = [...inputFields];
		if(!values[index]) return;
		values[index].driverName = event.target.value;
		setInputFields(values);
	}

	const handlePhoneNumber = (index, value) => {	
		const values = [...inputFields];
		if(!values[index]) return;
		values[index].phoneNumber = value;
		setInputFields(values);
	}

	const handleIdNumber = (index, event) => {	
		const values = [...inputFields];
		if(!values[index]) return;
		values[index].idNumber = event.target.value;
		setInputFields(values);
	}

	const handlePassportNumber = (index, event) => {	
		const values = [...inputFields];
		if(!values[index]) return;
		values[index].passportNumber = event.target.value;
		setInputFields(values);
	}

	const handlePlateNumber = (index, event) => {	
		const values = [...inputFields];
		if(!values[index]) return;
		values[index].plateNumber = event.target.value;
		setInputFields(values);
	}

	const handleTrailerNumber = (index, event) => {	
		const values = [...inputFields];
		if(!values[index]) return;
		values[index].trailerNumber = event.target.value;
		setInputFields(values);
	}

	const handleTrips = (index, value) => {	
		const values = [...inputFields];
		if(!values[index]) return;
		values[index].trips = value;
		setInputFields(values);
	}

	const handleRemoveFields = index => {
		const values = [...inputFields];
		if(!values[index]) return;
		let newTotal = grandTotal - values[index].total;
		let total = rate*newTotal;
		setTotalAmount(total);
		setGrandTotal(newTotal);
		values.splice(index, 1);
		setInputFields(values);
	};  
	const disabledDate = (current) => {
		return current && current > moment().endOf("day");
	}  

	const handleRateChange = (value) => {
		let total = value*grandTotal;
		setRate(value);
		setTotalAmount(total);
	}

	const handleTotalChange = (value) => {
		setGrandTotal(value);
	}

	const handleCurrencyChange = (currency) => {
		setSelectedCurrency(currency)
	}

	return (
		<>
		<div className={(visible) ? 'container d-none' : 'container'}>
			<Card>
				<Form
					//layout="horizontal"
					layout="vertical" hideRequiredMark form={form}
				>
					<div className="d-md-flex justify-content-md-between">
						<div className="">
							<h2 className="mb-3 font-weight-semibold">Truck rental form</h2>
							<Form.Item style={{ margin: 0, padding: 0 }}>
								<Form.Item
									name="companyName"
									label="Company name"
									rules={[{ required: true, message:"Enter company name" }]}
									style={{ display: 'inline-block', width: 'calc(60% - 5px)', marginRight: 8 }}
								>
									<Input placeholder="Company name" value={companyName} onChange={handleNameChange}/>
								</Form.Item>
								<Form.Item
									name="companyNumber"
									label="TIN or Phone number"
									min={0}
									rules={[{ required: true, message:"Enter TIN or Phone number" }]}
									style={{ display: 'inline-block', width: 'calc(40% - 5px)' }}
								>
									<InputNumber placeholder="TIN or Phone Number" className="w-100" value={companyNumber} onChange={handleNumberChange}/>
								</Form.Item>
							</Form.Item>
						</div>
					<div className="mt-3 text-right">
						<Form.Item name="dates">
							<DatePicker defaultValue={moment(new Date(), dateFormat)} format={dateFormat} disabledDate={disabledDate}  onChange={handleDateChange}/>
						</Form.Item>
						
					</div>
				</div>
				<div className="mt-5 mb-5">
				{inputFields.map((inputField, index) => (
				<Row gutter={16} className="w-100 border-bottom mb-3" key={`${inputField}~${index}`}>
					<Col span={6}>
						<Form.Item name={`driverName${index}`} label="Driver name" rules={[
							{
							required: true,
							message: `Item is required.`,
							},
						]}>
							<Input
								className="w-100"
								value={inputField.driverName}
								onChange={value => handleDriverName(index, value)}
							/>
						</Form.Item>
					</Col>
					
					<Col span={6}>
						<Form.Item name={`idNumber${index}`} label="Id/Passport Number" rules={[
							{
							required: true,
							message: `This field is required.`,
							},
						]}>
							<Input className="w-100"
								value={inputField.idNumber}
								onChange={value => handleIdNumber(index, value)}
							/>
						</Form.Item>
					</Col>
					
					<Col span={6}>
						<Form.Item name={`phoneNumber${index}`} label="Phone Number" rules={[
							{
							required: true,
							message: `This field is required.`,
							},
						]}>
							<InputNumber className="w-100" min={0} 
										parser={value => value.replace(/\$\s?|(,*)/g, '')}
										formatter={value => `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, '')}
										value={inputField.phoneNumber}
										onChange={value => handlePhoneNumber(index, value)}/>
						</Form.Item>
					</Col>
					<Col span={6}>
						<Form.Item name={`plateNumber${index}`} label="Plate Number" rules={[
							{
							required: true,
							message: `Plate number is required.`,
							},
						]}>
							<Input
								className="w-100"
								value={inputField.plateNumber}
								onChange={value => handlePlateNumber(index, value)}
							/>
						</Form.Item>
					</Col>
					<Col span={6}>
						<Form.Item name={`trailerNumber${index}`} label="Trailer Number" rules={[
							{
							required: true,
							message: `Trailer number is required.`,
							},
						]}>
							<Input
								className="w-100"
								value={inputField.trailerNumber}
								onChange={value => handleTrailerNumber(index, value)}
							/>
						</Form.Item>
					</Col>
					<Col span={0}>
						<Form.Item label="Trips">
							<InputNumber className="w-100" min={1} 
								value={inputField.trips} 
								formatter={value => `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ',')}
								onChange={value => handleTrips(index, value)}
							/>
						</Form.Item>
					</Col>
					<Col span={3}>
						{inputFields.length > 1 ? (
							<Form.Item label=" ">
								<MinusCircleOutlined
								className="ml-2"
								style={{fontSize: '24px'}}
								onClick={() => handleRemoveFields(index)}
								/>
							</Form.Item>
						) : null}	
					</Col>
				</Row>
				))}		
					<Form.Item>
						<Button
						type="dashed"
						onClick={() => handleAddFields()}
						>
						<PlusOutlined /> Add field
						</Button>
					</Form.Item>  
					<div className="d-flex justify-content-end mt-4">
						{/* <div className="text-right ">
							<div className="border-bottom text-left">
								<Form.Item 
									name="grandTotal" 
									label="Total Price" 
									rules={[{ required: true,message: `Total price is required` }]}
								>
									<InputNumber
										className="w-100"
										min={0}
										value={grandTotal}
										onChange={handleTotalChange}
										parser={value => value.replace(/\$\s?|(,*)/g, '')}
										formatter={value => `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ',')}
									/>
								</Form.Item>
								<Form.Item style={{ margin: 0, padding: 0 }}>
									<Form.Item 
										name="currency" 
										label={`Select Currency \n Used`} 
										rules={[{ required: true,message: `Currency is required` }]}
										style={{ display: 'inline-block', width: 'calc(50% - 5px)', marginRight: 8 }}
									>
										<Select
											placeholder="- Select Currency Used -"
											onChange={handleCurrencyChange}
										>
											{!currenciesLoading ? currencies.docs.map((currency,index)=><Option value={currency.data()._id} key={index} className='w-100 pe-5'>{currency.data().name}&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</Option>) : null }
										</Select>
									</Form.Item>
									<Form.Item 
										name="exchangeRate" 
										label="Exchange Rate vs 1RWF" 
										rules={[{ required: true,message: `Exchange rate is required` }]}
										style={{ display: 'inline-block', width: 'calc(50% - 5px)' }}
									>
										<InputNumber
											className="w-100"
											min={0}
											onChange={handleRateChange}
											parser={value => value.replace(/\$\s?|(,*)/g, '')}
											formatter={value => `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ',')}
										/>
									</Form.Item>
								</Form.Item>
							</div>
							<h2 className="font-weight-semibold mt-3">
								<span className="mr-1">Grand Total: </span>
								<NumberFormat 
									displayType={'text'} 
									value={grandTotal} 
									suffix={(allCurrencyLoading || !selectedCurrency) ? '' : allCurrency[selectedCurrency]?.name}
									thousandSeparator={true}
								/>
							</h2>
							{totalAmount ? <h2 className="font-weight-semibold mt-3">
								<span className="mr-1">Total in RWF: </span>
								<NumberFormat 
									displayType={'text'} 
									value={totalAmount} 
									suffix={'RWF'}
									thousandSeparator={true}
								/>
							</h2> : null }
						</div> */}
					</div>
					
				</div>
				<hr className="d-print-none"/>
				<div className="text-right d-print-none">
					<Button type="secondary" className="mr-2" onClick={() => RedirectBack()}>
						<CloseCircleOutlined /> Cancel
					</Button>
					<Button type="primary" loading={buttonLoading} disabled={buttonLoading} onClick={handleSubmit}>
						<SaveOutlined  />
						Save
					</Button>
				</div>
				</Form>
			</Card>
		</div>
		</>
	);	
}

export default Sales
